
// import Intro from '@/components/Intro.vue'; // @ is an alias to /src
import {computed, defineComponent, ref} from 'vue';
import StepProgressBar from "@/components/StepProgressBar.vue";
import CheckAccount from "@/components/CheckAccount.vue";
import RequestComplete from "@/components/RequestComplete.vue";
import SelectBank from "@/components/SelectBank.vue";
import Remote from "@/remote";
import TAndC from "@/components/common/TAndC.vue";
import { useStore } from 'vuex';
import {GENERAL_ERROR, getErrorMessageFromCode, PRO_ERROR} from "@/components/constant";

export default defineComponent({
  name: 'Request',
  $refs: {
    StepProgressBarRef: StepProgressBar
  },
  components: {
    SelectBank,
    StepProgressBar,
    CheckAccount,
    RequestComplete,
    TAndC
    // Intro,
  },
  data () {
    return {
      checkingRequest: false,
      tcConfirmed: false,
      isMinimumAmountError: false,
      requestFail: false,
      errorMessage: '',
      hasUnapprovedTimesheets: false
    }
  },

  setup() {
    const limited = ref(false)
    const step = ref(1)
    const hasChecked = ref(false)
    const availableAmount = ref(0)
    const requestAmount = ref<number | null>()
    let $store = useStore()
    const minimumRequestAmount = computed(() => $store.state.minimumRequestAmount)
    const nextPaymentDate = computed(() => $store.state.nextPaymentDate)

    return { limited, step, hasChecked, availableAmount, requestAmount, minimumRequestAmount, nextPaymentDate }
  },

  mounted() {
    if (this.$store.state.featureChecked === false) {
      this.$router.push('/')
    }
  },

  methods: {
    pickStep(newStep: number) {
      this.step = newStep;
      console.log('picked in request', newStep)
    },

    checkAvailable() {
      //
      if (!this.requestAmount) {
        return;
      }

      this.isMinimumAmountError = false;
      this.requestFail = false;
      this.errorMessage = '';

      if (this.$store.state.minimumRequestAmount > this.requestAmount) {
        console.error('must bigger then min amount');
        this.isMinimumAmountError = true;
        return;
      }

      this.checkingRequest = true;
      Remote.RequestApproval(this.requestAmount!)
      .then((res) => {
        if (res.Response.IsOverLimit) {
          this.limited = true;
          this.requestAmount = res.Response.Limit;
          this.availableAmount = res.Response.Limit;
        } else {
          //
          this.availableAmount = res.Response.RequestAmount
        }

        if (res.Response.HasUnapprovedTimesheets) {
          this.hasUnapprovedTimesheets = res.Response.HasUnapprovedTimesheets
        }

        this.hasChecked = true;

        this.$store.state.requestAmount = this.requestAmount;

      }, err => {
        // console.error('err', err)
        this.requestFail = true;
        this.checkingRequest = false;
        if (err.response.data.Response?.ErrorCode) {
          if (err.response.data.Response?.ErrorCode === PRO_ERROR.AMOUNT_AVAILABLE_LESS_THAN_MIN_CONFIGURED) {
            this.errorMessage = `The current amount available $${err.response.data.Response.Limit} is less than the minimum request amount $${this.$store.state.minimumRequestAmount}. Please try again later.`
          } else {
            this.errorMessage = getErrorMessageFromCode(err.response.data.Response.ErrorCode, this.$store.state.nextPayCycleDate, this.$store.state.lastRequestDate)
          }
        } else {
          this.errorMessage = GENERAL_ERROR
        }

      })
      .finally(() => {
        this.checkingRequest = false;
      })

      console.log('checked available')
    },

    nextStep() {
      if (this.step < 4 ) {
        this.step++;
      } else {
        this.step = 1;
      }
      (this.$refs.StepProgressBarRef as typeof StepProgressBar).pick(this.step)
    },
    backStep() {
      this.step--;
      (this.$refs.StepProgressBarRef as typeof StepProgressBar).pick(this.step)

    },
    gotoDashBoard() {
      if (window.location.origin === 'https://payadvance.testaffinitylogon.com' ) {
        window.location.href = 'https://dashboard.testaffinitylogon.com/'
      } else {
        window.location.href = 'https://dashboard.affinitylogon.com/'
      }
    },

    checkAccountComplete() {
      // todo fire final request
      console.log('fire final request')
      this.step++
    },

    openTC() {
      console.log('clicked');
      (this.$refs.tcRef as any).toggleTC();
    }
  }
})
