<template>
  <div class="aff-card card-width pa-9 mt-9">
    <!--    choose phone or email -->
    <div>
      <div class="mfa-heading">
        <h3 class="ma-0 text-grey">Confirm your identity
          <span class="font-normal">by entering a confirmation code.</span>
        </h3>
      </div>

      <div v-if="!sentCode">
<!--      <div>-->
        <p class="mt-9 precede1 text-grey">
          <span v-if="isContactPhone">Send code to my phone.</span>
          <span v-else>Send a code to my registered email address.</span>
        </p>
        <div v-if="isLoadingAccount">
          <div class="loading-spin-icon"></div>
        </div>
        <div v-else>
          <div v-if="isContactPhone">
            <span class="account-space" v-if="phoneNumbers.length === 1">{{ phoneNumbers[0] }}</span>
            <span class="account-space" v-else>
              <select class="account-select" v-model="selectedMobile">
                <option class="account-option" v-for="number in phoneNumbers">{{ number }}</option>
              </select>
            </span>
            <button class="btn-default ml-4" @click="sendCode">Send Code</button>
          </div>
          <div v-else>
            <span class="account-space" v-if="emailAdds.length === 1">{{ emailAdds[0] }}</span>
            <span class="account-space" v-else>
              <select class="account-select" v-model="selectedEmail">
                <option class="account-option" v-for="email in emailAdds">{{ email }}</option>
              </select>
            </span>
            <button class="btn-default ml-4" @click="sendCode">Send Code</button>
          </div>
        </div>
        <div class="change-test-method">
          <span v-if="isContactPhone && emailAdds?.length > 0"  @click="changeTestMethod">Email my code</span>
          <span v-if="!isContactPhone && phoneNumbers?.length > 0" @click="changeTestMethod">Send code to my phone</span>
        </div>
      </div>
      <div v-else>
<!--      <div>-->
        <div class="mt-10" v-if="!verified && !finalizing">
          Confirmation code sent to {{ sentTarget }}
        </div>
        <div class="mt-3 ">
          <div class="code-box" v-if="!verified">
            <div v-for="(currentKey, i) in confirmNumber" :key="i">
              <input :ref="el => { if (el) confirmNumberRef[i] = el }"
                  @click="$event.target.select()" @keydown="nextKey($event.key, confirmNumberRef[i] , i)"
                  v-model.number="confirmNumber[i]">
              <span v-if="i !== confirmNumber.length - 1" class="code-box-spacer"></span>
            </div>
          </div>
          <button v-if="!verifying && !finalizing && !verified && (!errorMessage || showVerifyCodeError)" class="btn-default verifyCodeButton ml-4 copy1" @click="verifyCode">Verify Code</button>
          <div v-if="!finalizing">
            <div v-if="!verifying && errorMessage" class="mt-4 py-0 pr-4">
              <span class="red" v-if="!backendErrorMessage">Code verification failed, please check your message and then try again</span>
              <span class="red" v-if="backendErrorMessage">{{errorMessage}}</span>
            </div>
            <div v-if="verifying" class="mt-4 py-0 pl-2 pr-4">
              <span class="mr-2">
                <span class="iconify spin" data-inline="false" data-icon="ei:spinner" style="color: #0c96e4; font-size: 30px;"></span>
              </span>
              <b>Verifying your code....</b>
            </div>
          </div>
          <div v-if="verified && !finalizing" class="mt-4 py-0 pr-4">
            <div>
              <span>Code verified. Please click the Confirm button to complete the request.</span>
            </div>

            <button class="btn-default mt-4" @click="finalizePayAdvance">Confirm</button>
          </div>
          <div v-if="finalizing" class="mt-4 py-0 pl-2 pr-4">
            <span class="mr-2">
              <span class="iconify spin" data-inline="false" data-icon="ei:spinner" style="color: #0c96e4; font-size: 30px;"></span>
            </span>
            <b>Finalising your pay advance request...</b>
          </div>
        </div>
        <div class="button-blue mt-5 caption resend-code" v-if="showResend" @click="sendCode">
          Send me a new code
        </div>
<!--        <div class="code-error py-2 pl-3 mt-5 precede1" v-if="errorMessage">-->
<!--          {{errorMessage}}-->
<!--        </div>-->
      </div>
    </div>
    <payment-amount-heading></payment-amount-heading>
  </div>
</template>

<script lang="ts">

import PaymentAmountHeading from "@/components/PaymentAmountHeading.vue";
import {defineComponent, ref,} from "vue";
import Remote from "@/remote";
import { debounce } from 'lodash';
import {GENERAL_ERROR, getErrorMessageFromCode} from "@/components/constant";


export default defineComponent({
  name: "check-account",
  components: {PaymentAmountHeading},
  data() {
    return {
      isSendingCode: false,
      errorMessage: '',
      backendErrorMessage: false,
      showResend: false,
      verifying: false,
      verified: false,
      finalizing: false,
      showVerifyCodeError: false,
    }
  },
  setup() {
    const isLoadingAccount = ref(false);
    const isContactPhone = ref(true);
    // const phoneNumbers = ref([123456789, 23456789])
    const phoneNumbers = ref<string[]>([]);
    const selectedMobile = ref();
    // const emailAdds = ref(['example@test.com', 'example2@test.com'])
    const emailAdds = ref<string[] | null>([]);
    const selectedEmail = ref();
    const sentCode = ref(false);
    const sentTarget = ref(phoneNumbers.value[0]);
    const confirmNumber = ref<(number | null)[]>([null, null, null, null, null, null])

    const key1Ref = ref<HTMLInputElement | null>(null)
    const key2Ref = ref<HTMLInputElement | null>(null)
    const key3Ref = ref<HTMLInputElement | null>(null)
    const key4Ref = ref<HTMLInputElement | null>(null)
    const key5Ref = ref<HTMLInputElement | null>(null)
    const key6Ref = ref<HTMLInputElement | null>(null)
    const confirmNumberRef = ref<any[]>([])

    let nextKey = debounce(function (key: string, templateRef: HTMLInputElement, index: number) {
      if (key === 'Backspace' || key === 'Delete') {
        if (index > 0) {
          confirmNumberRef.value[index - 1].select()
        }
        return;
      }

      if (!['1', '2', '3', '4', '5', '6', '7', '8', '9', '0'].includes(key)) {
        return;
      }

      // set input value, fix press multi key at same time.
      confirmNumber.value[index] = Number(key)

      if (index < 5) {
        confirmNumberRef.value[index + 1].select();
      } else {
        confirmNumberRef.value[index].blur();
      }

    }, 20)

    return {
      isContactPhone, phoneNumbers, emailAdds, sentTarget, sentCode, confirmNumber, isLoadingAccount, selectedMobile, selectedEmail,
      key1Ref, key2Ref, key3Ref, key4Ref, key5Ref, key6Ref, confirmNumberRef,
      nextKey,
    }
  },

  mounted() {
    this.loadMFA()
  },

  methods: {
    async loadMFA() {

      this.isLoadingAccount = true;
      try {
        let mfaResponse = await Remote.GetMFAChannels()
        if (mfaResponse.Response.MobileNumbers && mfaResponse.Response.MobileNumbers.length > 0) {
          this.phoneNumbers = mfaResponse.Response.MobileNumbers;
          this.selectedMobile = this.phoneNumbers[0];
          this.isContactPhone = true;

          if (mfaResponse.Response.EmailAddresses?.length > 0) {
            this.emailAdds = mfaResponse.Response.EmailAddresses;
            this.selectedEmail = this.emailAdds![0];
          }
        } else if (mfaResponse.Response.EmailAddresses && mfaResponse.Response.EmailAddresses.length > 0) {
          this.emailAdds = mfaResponse.Response.EmailAddresses;
          this.selectedEmail = this.emailAdds![0];
          this.isContactPhone = false;

          if (mfaResponse.Response.MobileNumbers.length > 0) {
            this.phoneNumbers = mfaResponse.Response.MobileNumbers;
            this.selectedMobile = this.phoneNumbers[0];
          }
        }
      } catch (e) {
        console.error(e)
      }

      this.isLoadingAccount = false;
    },

    verifyCode() {
      this.verifying = true;
      this.showVerifyCodeError = false;
      this.errorMessage = '';
      let queryData = {
        code: this.confirmNumber.join(""),
        mfaChannel: this.isContactPhone ? this.selectedMobile : this.selectedEmail
      }

      Remote.VerifyMFACode(queryData)
      .then(res => {
        if (res.Response?.IsSuccessful) {
          this.verified = true;
          this.showResend = false;
        } else {
          //
          this.errorMessage = 'Something goes wrong, please try again later'
        }
      }, err => {
        const Response = err.response.data.Response;
        this.showVerifyCodeError = true;
        if (Response?.IsSuccessful === false) {
          if (Response?.ErrorMessage === 'Invalid Code') {
            this.errorMessage = 'Code verification failed.'
          }
        }
      })
      .finally(() => {
        this.verifying = false
      })
    },

    finalizePayAdvance() {
      this.finalizing = true;
      this.errorMessage = '';

      let data = {
        selectedBank: this.$store.state.selectedBank,
        amountRequested: this.$store.state.requestAmount,
      }

      Remote.RequestPayAdvance(data).then((res) => {
        if (res.Response) {
          this.$emit('checkAccountComplete')
        } else {
          // todo: change to correct restful error
          console.error(res)
          this.errorMessage = GENERAL_ERROR
        }
      }, err => {
        console.error('err', err)
        if (err.response.data.Response.ErrorCode) {
          this.backendErrorMessage = true

          this.errorMessage = getErrorMessageFromCode(err.response.data.Response.ErrorCode, this.$store.state.nextPayCycleDate, this.$store.state.lastRequestDate)
        } else {
          this.errorMessage = GENERAL_ERROR
        }

      })
      .finally(() => {
        this.finalizing = false;
        this.verified = false;
      })
    },

    sendCode() {
      this.isSendingCode = true;
      this.showResend = false;
      console.log('test send code')
      this.sentCode = true;

      let postData: any = {}

      if (this.isContactPhone) {
        postData.MobileNo = this.selectedMobile
      } else {
        postData.Email = this.selectedEmail
      }

      console.log('postData', postData)

      Remote.SendMFACode(postData)
      .then(() => {
        if (this.isContactPhone) {
          this.sentTarget = this.selectedMobile
        } else {
          this.sentTarget = this.selectedEmail
        }
        setTimeout(() => {
          this.showResend = true;
        }, 5000)

      },(e) => {
        // todo: handle send code error.
      }).finally(() => {
        this.isSendingCode = false;
        this.sentCode = true;
      })
      this.sentCode = true;

    },
    changeTestMethod() {
      this.isContactPhone = !this.isContactPhone;
    }
  }
})
</script>

<style scoped lang="scss">
@use '../style/colors';
@use '../style/layouts';

.card-width {
  width: 1040px;
  height: 340px;
  box-sizing: border-box;

  @media screen and (max-width: layouts.$md-screen) {
    width: 100%;
    padding: 18px;
  }
}

.mfa-heading {
  @media screen and (max-width: layouts.$md-screen) {
    width: 60%;
  }

  @media screen and (max-width: layouts.$xs-screen) {
    width: 100%;
  }
}

.account-space {
  display: inline-block;
  font-weight: bold;
  font-size: 18px;
  color: colors.$text-grey;
  width: 300px;

}

.account-select {
  display: inline-block;
  width: 100%;
  padding: 6px 5px;
  font-weight: bold;
  font-size: 18px;
  color: colors.$text-grey;
  border-radius: 4px;
  vertical-align: middle;
}

.account-option {
  display: inline-block;
  font-weight: bold;
  font-size: 14px;
  color: colors.$text-grey;
}

.code-box {
  width: 264px;
  height: 45px;
  display: inline-flex;
  justify-content: space-between;
  align-items: center;
  background: #F6F9FF;
  border: 1px solid #C8D1DB;
  box-sizing: border-box;
  vertical-align: middle;

  input {
    width: 40px;
    text-align: center;
    margin: 0;
    padding: 0;
    border: none;
    font-weight: bold;
    font-size: 28px;
    background: transparent;

    &:focus {
      outline: none;
    }
  }

  .code-box-spacer {
    border-left: 1px solid #DADADA;
    height: 30px;
  }
}

.resend-code {
  text-decoration: underline;
  cursor: pointer;
}

.success-confirm {
  color: colors.$green;
  background: #F6FFED;
  border: 1px solid #CDE7B9;
  border-radius: 4px;
}

.code-error {
  color: colors.$orange;
  background: #FFFBE6;
  border: 1px solid #FCE59A;
  border-radius: 4px;
}

.change-test-method {
  margin-top: 20px;
  color: colors.$aff-blue;
  font-size: 12px;
  text-decoration: underline;
  cursor: pointer;

  &:hover {
    opacity: 0.7;
  }
}

.verifyCodeButton {
  height: 44px;

  b {
    vertical-align: middle;
  }
}

</style>