<template>
  <div>
    <h2 class="text-grey ma-0">Pay Advance</h2>
    <h1 class="mt-4 mb-0 text-grey">Config</h1>

    <div class="config-form mt-8 text-grey">
      <div class="error-section pa-4" v-if="errors.length > 0">
        <label class="red bold mb-2">Error:</label>
        <div class="mt-1" v-for="error in errors">
          <span class="red">* {{error}}</span>
        </div>
      </div>
      <div class="config-option">
        <label>Select ESS Group</label>

        <div class="custom-select" tabindex="1"  @blur="open = false">
          <div class="custom-select-selected" :class="{ open: open }" @click="open = !open">
            {{essSelected.Name}}
          </div>
          <div class="custom-select-items" :class="{ 'custom-select-hide': !open }">
            <div class="custom-select-item"
                 v-for="(option, i) of essGroups" :key="i"
                 @click="essSelected = option; open = false; selectNewGroup();">
                {{option.Name}}
            </div>
          </div>
        </div>
      </div>
      <div>
        <div v-if="isLoading" class="mt-9">
          <div class="loading-spin-icon"></div>
        </div>
        <div v-else>
          <div class="config-option">
            <label>Enabled</label>
            <!--        <input type="checkbox">-->
            <label class="cust-box-container darkblue">
              <input type="checkbox" v-model="essSelected.enabled">
              <span class="cust-box-checkmark"></span>
            </label>
          </div>
          <div class="config-option">
            <label>Percentage<span class="red"> *</span></label>
            <input type="number" class="number-input" max="99" min="1" @input="updateNumber" v-model.number="essSelected.percentage">
          </div>
          <div class="config-option">
            <label>Minimum Request Amount<span class="red"> *</span></label>
            <input type="number" class="number-input" min="1" @input="updateRequestAmount" v-model.number="essSelected.minimumRequestAmount">
          </div>
        </div>

      </div>
      <div class="mt-9 config-button-bottom" v-if="!isLoading" >
        <div v-if="!essSelected.configured">
          <div class="precede2 text-grey">Group config has not been created, please create first</div>
          <button class="btn-blue mt-4" @click="createConfig">Create</button>
        </div>
        <div v-else class="d-flex justify-space-between ">
          <button class="btn-orange" @click="updateConfig">Update</button>
          <button class="btn-default" @click="deleteConfig" v-if="essSelected.Name !== 'All Company'">Delete</button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import {defineComponent, ref} from "vue";
import Remote from "@/remote";
import {useRouter} from "vue-router";

export default defineComponent({
name: "Config.vue",
  data() {
    return {
      openEssGroupSelect: false,
      open: false,
      openRequestType: false,
      isLoading: false,
      errors: [],
    }

  },
  setup() {
    const router = useRouter()
    const essGroups = ref([{
      Name: 'default group',
      Id: 'E001'
    }, {
      Name: 'group2',
      Id: 'E002'
    }])
    const essSelected = ref({})
    essSelected.value = essGroups.value[0]

    Remote.CheckIsAdmin().then(res => {
      if (!res.data.Response) {
        router.push('/')
      }
    })

    return { essGroups, essSelected }
  },

  mounted() {
    this.isLoading = true;
    Remote.GetConfigEssGroups()
    .then(res => {
      console.log('res', res)
      this.essGroups = res.Response.map(name => {
        return {
          Name: name
        }
      });
      this.essSelected = this.essGroups[0]



      return Remote.GetGroupConfig(this.essSelected.Name)
    })
    .then(res => {
      console.log('res', res)
      this.essSelected.Id = res.Response.GroupId;
      this.essSelected.configured = res.Response.IsConfigured;
      this.essSelected.enabled = res.Response.Enabled;
      this.essSelected.percentage = res.Response.PayPercentage;
      this.essSelected.minimumRequestAmount = res.Response.MinimumRequestAmount;
    })
    .finally(() => {
      this.isLoading = false;
    })
  },

  methods: {
    updateNumber($event) {
      this.errors = [];
      if ($event.target?.value) {
        if ($event.target.value > 100) {
          this.essSelected.percentage = $event.target.value = 100

        } else if ($event.target.value < 1) {
          this.essSelected.percentage = $event.target.value = 1
        }
      }
    },

    updateRequestAmount($event) {
      this.errors = [];
      if ($event.target?.value && $event.target.value < 1) {
        this.essSelected.minimumRequestAmount = $event.target.value = 1
      }
    },

    selectNewGroup() {
      this.isLoading = true;
      this.errors = [];

      Remote.GetGroupConfig(this.essSelected.Name)
      .then(res => {
        this.errors = [];
        this.essSelected.Id = res.Response.GroupId;
        this.essSelected.configured = res.Response.IsConfigured;

        if (this.essSelected.configured) {
          this.essSelected.enabled = res.Response.Enabled;
          this.essSelected.percentage = res.Response.PayPercentage;
          this.essSelected.minimumRequestAmount = res.Response.MinimumRequestAmount;

        } else {
          this.essSelected.enabled = true;
          this.essSelected.percentage = 60;
          this.essSelected.minimumRequestAmount = 15;
        }
      })
      .finally(() => {
        this.isLoading = false;
      })
    },

    validateConfig() {
      this.errors = [];
      // min required must bigger then 0
      if (!this.essSelected.percentage && this.essSelected.minimumRequestAmount !== 0 ) {
        this.errors.push('Percentage field is required.')
      }

      if (!this.essSelected.minimumRequestAmount && this.essSelected.minimumRequestAmount !== 0) {
        this.errors.push('Minimum request amount field is required.')
      }

      // percentage required must bigger then 0
      if (this.essSelected.percentage === 0) {
        this.errors.push('Percentage field must greater then 0.')
      }

      if (this.essSelected.minimumRequestAmount === 0) {
        this.errors.push('Minimum request amount field must greater or equal to 1.')
      }

      return this.errors.length <= 0;
    },

    createConfig() {
      if (!this.validateConfig()) return;

      this.isLoading = true;
      Remote.CreateGroupConfig(this.essSelected)
      .then(res => {
        this.selectNewGroup()
      }, err => {
        this.isLoading = false;
      })
    },

    updateConfig() {
      if (!this.validateConfig()) return;

      this.isLoading = true;
      Remote.UpdateGroupConfig(this.essSelected)
      .then(res => {
        this.selectNewGroup()
      }, err => {
        console.error(err)
        this.isLoading = false;
      })
    },
    deleteConfig() {
      this.isLoading = true;
      Remote.DeleteConfig(this.essSelected)
        .then(res => {
          console.log('res', res)
          this.selectNewGroup()
        }, err => {
          console.error(err)
          this.isLoading = false;
        })
    }
  }

})
</script>

<style scoped lang="scss">
@use '../style/colors';
  input {
    margin: 0;
  }

  .number-input {
    width: 60px;
    padding: 5px 10px;
    display: inline-block;
    border: 1px solid colors.$dark-blue-app;
    border-radius: 4px;
    box-sizing: border-box;
    background: transparent;
    text-align: center;

    &:focus {
      outline: none;
    }
  }

  .config-form {
    font-size: 14px;

    // display: inline-block;
    label {
      display: inline-block;
      width: 150px;
      vertical-align: middle;
    }

    .no-request {
      margin-left: 10px;
    }
  }

  .no-request-input {
    margin-left: 10px;
  }

  .config-option {
    margin-top: 20px;
  }

  .custom-select {
    display: inline-block;
  }

  .custom-select {
    width: auto;
    min-width: 220px;
    //height: 36px;
    //line-height: 36px;
  }

  .custom-select-selected {
    padding-right: 4em;

    &:after {
      top: 15px;
    }
  }

  .config-button-bottom {
    max-width: 370px;
  }

  .error-section {
    color: colors.$red;
    background: lighten(colors.$red, 60%);
    border: 1px solid lighten(colors.$red, 40%);
  }
</style>