<template>
  <div class="Pay-History">
    <p>Pay Advance</p>
    <h1 class="ma-0 mt-4">Pay History</h1>
    <div class="history-table aff-card mt-6">
      <div class="table-heading history-table-grid precede2">
        <div class="heading-gap"></div>
        <div class="table-heading">Request ID</div>
        <div class="table-heading">Amount ($)</div>
        <div class="table-heading">Date Requested</div>
        <div class="table-heading">Paid to Account</div>
      </div>
      <div class="history-table-grid mt-5" v-for="record in historyRecords" :index="record.Id">
        <span class="plus-file-icon"></span>
        <span class="history-grid-item copy2">{{ record.Id }}</span>
        <span class="history-grid-item copy1">{{ record.RequestedAmount.toFixed(2) }}</span>
        <span class="history-grid-item copy2">{{ requestDateFilter(record.TimeRequested) }}</span>
        <span class="history-grid-item copy2">
<!--          <span class="copy1">Commonwealth (Everyday Account)</span> <br>-->
          <span>{{ record.BankAccount }}</span>
        </span>
      </div>
    </div>
    <div class="mobile-show">
      <div class="history-table-mobile aff-card mt-6" v-for="record in historyRecords" :index="record.Id">
        <div class="mobile-icon-column">
          <span class="plus-file-icon"></span>
        </div>

        <div class="table-heading history-table-grid precede2">
          <div class="history-grid-item">Request ID</div>
          <span class="history-grid-item font-normal">{{ record.Id }}</span>
          <div class="history-grid-item">Amount</div>
          <h3 class="history-grid-item">{{ record.RequestedAmount.toFixed(2)}}</h3>
          <div class="history-grid-item">Date Requested</div>
          <span class="history-grid-item font-normal">{{ requestDateFilter(record.TimeRequested) }}</span>
          <div class="history-grid-item">Paid to Account</div>
          <span>{{ record.BankAccount }}</span>
        </div>
        <div class="history-table-grid mt-5" >

          <span class="history-grid-item copy2">
<!--          <span class="copy1">Commonwealth (Everyday Account)</span> <br>-->

        </span>
        </div>
      </div>
    </div>

    <div class="history-pag-wrapper d-flex justify-end mt-9">
      <div class="history-pagination d-flex justify-space-between align-center">
        <span class="mr-2">Page:</span>
        <span class="pag-nav-arrow-wrapper" v-bind:class="{ 'disabled': currentPageNo === 1}"
              @click="choosePage(currentPageNo - 1)"
              v-if="totalPageNo > 5">
        <span class="iconify" data-icon="ant-design:left-outlined"  data-inline="false"></span>
        </span>

        <div class="history-pag-page"
             v-bind:class="{'selected' : pageNo === currentPageNo}"
             v-for="pageNo in getPageNoList()"
             :key="pageNo" @click="choosePage(pageNo)">{{pageNo}}</div>
        <span class="pag-nav-arrow-wrapper" v-bind:class="{ 'disabled': currentPageNo === totalPageNo}"
              @click="choosePage(currentPageNo + 1)"
              v-if="totalPageNo > 5">
        <span class="iconify" data-icon="ant-design:right-outlined"  data-inline="false"></span>
        </span>
      </div>
    </div>


  </div>
</template>

<script lang="ts">
import { defineComponent, ref } from 'vue';
import Remote from "@/remote";
import moment from "moment";

export default defineComponent({
  name: 'History',
  setup() {
    const currentPageNo = ref(1); // default first page
    const totalPageNo = ref(10); // default size of No. of pages return from query
    const pageSize = ref(40);
    const historyRecords = ref([])

    let request = {
      page: currentPageNo.value,
      size: pageSize.value
    }
    Remote.GetHistory(request)
    .then(res => {
      historyRecords.value = res.Response.Records;
      totalPageNo.value = res.Response.TotalPage;
      currentPageNo.value = res.Response.CurrentPage;
    })

    return {
      currentPageNo,
      totalPageNo,
      pageSize,
      historyRecords,
    }
  },
  computed: {
    // requestDateFilter(dateString: string) {
    //   return moment(dateString).format('DD / MM / YYYY')
    // }
  },
  methods: {
      // <div class="history-pag-page" v-for="pageNo in getpageNoList" :key="pageNo">{{pageNo}}</div>
    getPageNoList() {

      if (this.totalPageNo <= 5) {
        return this.getRange(1, this.totalPageNo)
      }

      if (this.currentPageNo <= 3) {
        return this.getRange(1, 5); //array of 5
      } else if (this.currentPageNo > 3 && this.currentPageNo < this.totalPageNo - 3) {
        return this.getRange(this.currentPageNo - 2, this.currentPageNo + 2)
      } else {
        return this.getRange(this.totalPageNo - 4, this.totalPageNo)
      }
    },

    choosePage(pageNo: number) {
      console.log('page no', pageNo)
      this.currentPageNo = pageNo;

      let request = {
        page: this.currentPageNo,
        size: this.pageSize
      }

      Remote.GetHistory(request)
          .then(res => {
            this.historyRecords = res.Response.Records;
            this.totalPageNo = res.Response.TotalPage;
            this.currentPageNo = res.Response.CurrentPage;
          })
    },

    // get range with end + 1 (so start from 1)
    getRange(start: number, end: number) {
      console.log('start', start)
      console.log('end', end)
      let step = 1
      return Array.from({length: (end + 1 - start)}, (v, k) => k + start);
      // return Array.from({ length: (end - start) / step + 1}, (_, i) => start + (i * step));

    },

    requestDateFilter(dateString: string) {
      return moment(dateString).format('DD/MM/YYYY')
    }
  }
})
</script>
<style scoped lang="scss">
@use '../style/colors';
@use '../style/layouts';

$history-xs-screen: 780px;

  .Pay-History {
    color: colors.$text-grey;
  }

  .mobile-show {
    display: none;
    @media screen and (max-width: $history-xs-screen) {
      display: flex;
      justify-content: center;
      align-items: center;
      flex-direction: column;
    }
  }

  .history-table-mobile {
    padding: 20px;
    width: 100%;
    max-width: 400px;

    .mobile-icon-column {
      display: inline-flex;
      justify-content: center;
      align-items: center;
      width: 40px;
      height: 70px;
      margin-right: 20px;
    }

    .plus-file-icon {
      display: inline-block;
    }

    .history-table-grid {
      display: inline-grid;
      width: 70%;
      grid-template-columns: 130px auto;
      grid-template-rows: 1fr 1fr 1fr 1fr;
      gap: 1px 20px;
      font-family: Arial,Helvetica Neue,Helvetica,sans-serif;
    }
    //
    //.table-heading {
    //  display: grid;
    //  align-items: center;
    //  justify-items: start;
    //}
  }

  .history-table {
    padding: 35px;

    @media screen and (max-width: layouts.$md-screen) {
      width: 100%;
      padding: 18px;
      box-sizing: border-box;
    }

    @media screen and (max-width: $history-xs-screen) {
      display: none;
    }

  }

  .history-table-grid {
    display: grid;
    grid-template-columns: 20px 100px 100px 125px 300px;
    grid-template-rows: 1fr;
    gap: 1px 20px;
    font-family: Arial,Helvetica Neue,Helvetica,sans-serif;
  }

  .history-grid-item {
    display: grid;            /* new */
    align-items: center;      /* new */
    justify-items: start;    /* new */
  }

  .plus-file-icon {
    width: 18px;
    height: 24px;
    background: url("../style/icon/history-item-placeholder.svg");
    background-size: cover;
  }

  .history-pag-wrapper {
    max-width: 795px;

    @media screen and (max-width: $history-xs-screen) {
      justify-content: center;
    }
  }

  .history-pag-page {
    margin: 0 8px;
    padding: 5px 10px;
    border: 1px solid lighten(colors.$text-grey, 50%);
    border-radius: 4px;
    color: colors.$text-grey;
    cursor: pointer;

    &.selected {
      color: white;
      background: colors.$btn-blue;
    }
  }

  .pag-nav-arrow-wrapper {
    cursor: pointer;
  }

  .pag-nav-arrow-wrapper.disabled {
    opacity: 0.5;
    pointer-events: none;
  }
  .pag-nav-arrow-wrapper[disabled] {
    opacity: 0.5;
  }
</style>