<template>

  <modal ref="alertModalRef">
      <template v-slot:body>
        <div class="mr-2 d-inline" v-if="showLoader">
              <span class="mr-2">
                <span class="iconify spin" data-inline="false" data-icon="ei:spinner"
                      style="color: #0c96e4; font-size: 30px;"></span>
              </span>
        </div>
        <div class="text-grey" v-bind:style="{'line-height': '30px'}" v-html="message"></div>
      </template>
      <template v-slot:footer v-if="showButtons">
        <div class="mt-4">
          <button class="btn-orange btn-small" v-bind:class="{'mr-4' : showCancel}" @click="onOk">
            {{ confirmButtonText }}
          </button>
          <button class="btn-grey btn-small" v-if="showCancel" @click="onCancel">
            {{ cancelButtonText }}
          </button>
        </div>

      </template>
  </modal>
</template>

<script lang="ts">
// import {bus} from '../main'

import {defineComponent} from "vue";
import modal from "@/components/common/modal.vue";
import {emitter} from "@/main";
// import {emitter} from "@/main";

export default defineComponent({
  components: {modal},

  name: "Dialog",
  data() {
    return {
      show: false,
      dialog: false,
      message: `Here is a sample message `,
      showLoader: false,
      showButtons: false,
      showCancel: false,
      confirmButtonText: 'Ok',
      cancelButtonText: 'Cancel',
    }
  },
  mounted() {
    // (this as any).emitter.on('alert', (message: string) => {
    emitter.on('alert', (message: string | undefined) => {
      console.log('bus alert')
      this.dialog = true;
      this.message = message!;
      this.showLoader = false;
      this.showButtons = true;
      this.toggleShow();
    });

    emitter.on('dialog', (inputObj) => {

      // this.dialog = !!inputObj;
      this.toggleShow();
      this.message = inputObj.message;
      this.showCancel = inputObj.showCancel;
      this.showButtons = inputObj.showButtons;
      this.showLoader = inputObj.showLoader;
      this.confirmButtonText = inputObj.confirmButtonText ? inputObj.confirmButtonText : 'Ok'
      this.cancelButtonText = inputObj.cancelButtonText ? inputObj.cancelButtonText : 'Cancel'

      if (inputObj.onOk) {
        this.onOk = async () => {
          await inputObj.onOk();
          this.toggleShow()
        }
      }

      if (inputObj.onCancel) {
        this.onCancel = async () => {
          await inputObj.onCancel();
          this.toggleShow()
        }
      }
    })
    emitter.on('clearDialog', () => {
      console.log('bus clear dialog')
      this.dialog = false;
    });
  },
  methods: {
    toggleShow() {
      (this.$refs.alertModalRef as any).toggleShow();
    },
    onOk() {
      (this.$refs.alertModalRef as any).toggleShow();

    },
    onCancel() {
      (this.$refs.alertModalRef as any).toggleShow();

    },
  }
})
</script>

<style scoped lang="scss">

@use '../../style/layouts';
@use "../../style/colors";

.modal-mask {
  position: fixed;
  z-index: 9998;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  /*background-color: rgba(0, 0, 0, 0.5);*/
  display: table;
  transition: opacity 0.3s ease;
}

.modal-backdrop {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
}

.modal-wrapper {
  position: absolute;
  width: 100%;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  display: flex;
  vertical-align: middle;
  justify-content: center;
  align-items: center;
}

.modal-box {
  background: white;
  padding: 30px 40px;
  border-radius: 10px;
  box-shadow: colors.$box-shadow-1;
  //$box-shadow-1: 0px 5px 20px rgba(0, 55, 128, 0.2);

  transition: all 0.3s ease;
  z-index: 99999;
}

.modal-body {
  display: flex;
  justify-content: center;
}

.modal-footer {
  display: flex;
  justify-content: center;
}

/*
 * The following styles are auto-applied to elements with
 * transition="modal" when their visibility is toggled
 * by Vue.js.
 *
 * You can easily play with the modal transition by editing
 * these styles.
 */


.modal-enter {
  opacity: 0;
}

.modal-leave-active {
  opacity: 0;
}

.modal-enter-active {
  transition: all .3s ease-out;
}

.modal-leave-active {
  transition: all .3s ease-out;
}

.modal-enter-from,
.modal-leave-to {
  -webkit-transform: scale(1.1);
  transform: scale(1.1);
  opacity: 0;
}
</style>