<template>
  <div class="progress-bar-wrapper">
    <div class="status-bar" style="width: 69%;">
      <div class="current-status" v-bind:style="getStatusBarWidth(selectedNumber)" style="transition: width 300ms linear 0s;"></div>
    </div>
    <ul class="progress-bar">
      <li class="section step1"
          v-bind:class="getSelectedClass(1)"
          @click.ctrl="pickWithEmit(1)"
          style="width: 33%;">
          <span class="step-label">Enter <br class="show-xs">an Amount</span>
      </li>
      <li class="section step2"
          style="width: 33%;"
          @click.ctrl="pickWithEmit(2)"
          v-bind:class="getSelectedClass(2)">
          <span class="step-label">Select <br class="show-xs">an Account</span>
      </li>
      <li class="section step3"
          v-on:click.ctrl="pickWithEmit(3)"
          style="width: 33%;"
          v-bind:class="getSelectedClass(3)">
          <span class="step-label">Confirm <br class="show-xs">Your Identity</span>
      </li>
    </ul>
  </div>
</template>

<script>
import {defineComponent, ref, toRefs} from "vue";

export default defineComponent ({
  name: "step-progress-bar",
  props: ['selected'],
  setup(props) {
    const selectedNumber = ref(toRefs(props).selected.value)
    const test4 = ref(4)

    function pick(stepName) {
      this.selectedNumber = stepName;
    }

    function pickWithEmit(stepName) {
      if (process.env.VUE_APP_MODE === "MOCK") {
        console.log('pick', stepName)
        this.selectedNumber = stepName;
        this.$emit('pickStep', stepName)
      }
    }

    function getSelectedClass (targetStep) {
      switch (targetStep) {
        case 1:
          if (this.selectedNumber === 1) {
            return ['visited', 'current']
          } else if (this.selectedNumber === 2) {
            return 'visited'
          } else if (this.selectedNumber === 3) {
            return 'visited'
          }
          break;
        case 2:
          if (this.selectedNumber === 2) {
            return 'visited current'
          } else if (this.selectedNumber === 3) {
            return 'visited'
          }
          break;
        case 3:
          if (this.selectedNumber === 3) {
            return 'visited current'
          }
          break;
        default:
          return ''
      }
    }

    function getStatusBarWidth(selectedNumber) {
      if (this.selectedNumber === 1) {
        return '0';
      } else if (this.selectedNumber === 2) {
        return 'width: 50%';
      } else {
        return 'width: 100%';
      }
    }



    return {selectedNumber, pick, pickWithEmit, getSelectedClass, test4, getStatusBarWidth}
  },
  methods: {

  }
})
</script>

<style scoped lang="scss">
@use '../style/colors';
@use '../style/layouts';

ul.progress-bar {
  width: 100%;
  margin: 0;
  padding: 0;
  font-size: 0;
  list-style: none;
}

.progress-bar-wrapper {
  width: 900px;
  display: inline-block;

  @media screen and (max-width: layouts.$md-screen) {
    width: 100%;
    padding-bottom: 10px;
  }
}

li.section {
  display: inline-block;
  padding-top: 45px;
  font-size: 13px;
  font-weight: bold;
  line-height: 16px;
  color: #AAB0BB;
  vertical-align: top;
  position: relative;
  text-align: center;
  overflow: hidden;
  text-overflow: ellipsis;

  @media screen and (max-width: layouts.$xs-screen) {
    font-size: 10px;
    padding-top: 30px;
    line-height: normal;
  }
}

li.section:before {
  content: 'x';
  position: absolute;
  top: 2px;
  left: calc(50% - 15px);
  z-index: 1;
  width: 30px;
  height: 30px;
  color: white;
  border: 2px solid white;
  border-radius: 17px;
  line-height: 30px;
  background: #AAB0BB;

  @media screen and (max-width: layouts.$xs-screen) {
    width: 22px;
    height: 22px;
    line-height: 22px;
    left: calc(50% - 11px);
  }
}
.status-bar {
  height: 2px;
  width: 69%;
  background: #E2E6EB;
  position: relative;
  top: 20px;
  margin: 0 auto;
}
.current-status {
  height: 2px;
  width: 0;
  border-radius: 1px;
  background: colors.$aff-blue;
}

@keyframes changeBackground {
  from {background: #AAB0BB}
  to {background: colors.$aff-blue}
}

li.section.step1:before {
  content: '1';
}

li.section.step1.visited:before {
  animation: changeBackground .5s linear;
  animation-fill-mode: forwards;
}


li.section.step1:before {
  content: '1';
}

li.section.step2:before {
  content: '2';
}
li.section.step2.visited:before {
  animation: changeBackground .5s linear;
  animation-fill-mode: forwards;
}

li.section.step3:before {
  content: '3';

}

li.section.step3.visited:before {
  animation: changeBackground .5s linear;
  animation-fill-mode: forwards;
}


li.section.visited.current:before {
  box-shadow: 0 0 0 2px colors.$aff-blue;
}

li.section.visited {
  color: colors.$aff-blue
}

.step-label {
  @media screen and (max-width: layouts.$xs-screen) {
    display: inline-block;
    margin-top: 4px;
  }
}

.show-xs {
  display: none;
  @media screen and (max-width: layouts.$xs-screen) {
    display: inline;
  }
}

</style>