<template>
  <div class="aff-card mt-9 pa-10">
    <h3 class="text-grey bank-heading ma-0" v-if="bankAccounts && bankAccounts.length === 1">
      <span class="font-normal">Check that your <b>bank account</b> is correct.</span>
    </h3>
    <h3 class="text-grey bank-heading ma-0" v-else>
      <span>Select your Bank Account</span>
      <span class="ml-1">to send the money.</span>
    </h3>
    <div  v-if="isLoadingBank" class="mt-10">
      <div class="loading-spin-icon"></div>
    </div>
    <div v-else>
      <div class="mt-10 text-grey" v-if="bankAccounts && bankAccounts.length === 1">
        <div class="single-account">
          <span class="bold">{{ selected.accountNumber }} : </span>
          <span class="bank-branch">{{ selected.accountBankName }} - </span>
          <span class="bank-branch">{{ selected.accountBranch }}</span>
          <div class="mt-2">
            <span class="account-name" v-if="selected.accountLabel">{{selected.accountLabel}}</span>
          </div>
        </div>

      </div>
      <div class="mt-10" v-else>
        <!--  https://qvault.io/2020/09/25/how-to-make-a-custom-select-component-in-vue-js/-->
        <div class="custom-select multi-account" tabindex="1" @blur="open = false">
          <div class="custom-select-selected" :class="{ open: open }" @click="open = !open">
            <span class="bold">{{ selected.accountNumber }} : </span>
            <span class="bank-branch">{{ selected.accountBankName }} - </span>
            <span class="bank-branch">{{ selected.accountBranch }}</span>
            <div class="mt-2">
              <span class="account-name" v-if="selected.accountLabel">{{selected.accountLabel}}</span>
            </div>
          </div>
          <div class="custom-select-items" :class="{ 'custom-select-hide': !open }">
            <div class="custom-select-item"
                 v-for="(option, i) of bankAccounts" :key="i"
                 @click="selected = option; open = false; $emit('input', option); selectBank()">
<!--              <span v-if="option.accountLabel">{{option.accountLabel}} : </span><span>{{ option.accountNumber }}</span>-->
              <span class="bold">{{ option.accountNumber }} : </span>
              <span class="bank-branch">{{ option.accountBankName }} &nbsp;</span>
              <span class="bank-branch">{{ option.accountBranch }}</span>
              <div class="mt-1">
                <span class="account-name" v-if="option.accountLabel">{{option.accountLabel}}</span>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>

    <div class="account-fee">
      <span class="caption text-grey">The amount selected will be deducted from <b>your next pay, plus a $5 admin fee.</b></span>
    </div>
    <payment-amount-heading></payment-amount-heading>
  </div>
</template>

<script>
import PaymentAmountHeading from "@/components/PaymentAmountHeading";
import {computed, defineComponent, onMounted, ref} from "vue";
import Remote from "@/remote";
import {useStore} from "vuex";


export default defineComponent({
  name: "select-bank",
  components: {PaymentAmountHeading},
  data () {
    return {
      tabindex: 0,
      // selected: '123',
      open: false,
      // isLoadingBank: false,
    }
  },
  setup() {
    const bankAccounts = ref([{
      accountNumber: 1234567890,
      accountLabel: 'Main Account',
      branch: 'Takapuna Anazc Street',
      bankName: 'ANZ'
    }
    // , {
    //   accountNumber: 987654321,
    //   accountLabel: 'Account2',
    //   brand: 'asb'
    // }
    ])

    const isLoadingBank = ref(false);

    const selected = ref(bankAccounts.value[0]);
    console.log('selected', selected)
    let $store = useStore()
    const nextPaymentDate = computed(() => $store.state.nextPaymentDate)

    onMounted(() => {
      // the DOM element will be assigned to the ref after initial render
      // console.log('key1Ref', key1Ref.value) // <div>This is a root element</div>
      console.log('select bank mounted')
      isLoadingBank.value = true;
      Remote.GetBankAccounts()
          .then(res => {
            console.log('bank', res)
            bankAccounts.value = res.Response.map (account => {
              return {
                accountNumber: account.AccountNo,
                accountLabel: account.Label,
                accountBranch: account.Branch,
                accountBankName: account.BankName
              }
            })
            selected.value = bankAccounts.value[0];
            $store.state.selectedBank = selected.value.accountNumber;
          })
      .finally(() => {
        isLoadingBank.value = false;
      })
    })

    function selectBank() {
      $store.state.selectedBank = selected.value.accountNumber;
    }

    const options = ['123', '123123', '123123123']
    return {bankAccounts, options, selected, isLoadingBank, nextPaymentDate, selectBank}
  }
})
</script>

<style scoped lang="scss">
@use '../style/colors';
@use '../style/layouts';

.aff-card {
  position: relative;
  width: 1040px;
  height: 340px;
  box-sizing: border-box;

  @media screen and (max-width: layouts.$md-screen) {
    width: 100%;
    padding: 18px;
  }

  @media screen and (max-width: layouts.$xs-screen) {
    width: 100%;
  }
}

.bank-heading {
  @media screen and (max-width: layouts.$md-screen) {
    width: 60%;
  }
  @media screen and (max-width: layouts.$xs-screen) {
    width: 100%;
  }
}

.account-fee {
  position: absolute;
  bottom: 20px;

  @media screen and (max-width: layouts.$xs-screen) {
    bottom: 60px;
  }
}

.single-account {
  max-width: 500px;
  width: 50%;
  padding: 10px 20px;
  border: 1px solid #C8D1DB;
  background: #F6F9FF;
  border-radius: 4px;

  @media screen and (max-width: layouts.$xs-screen) {
    width: 80%;
  }
}

.multi-account {
  width: 50%;


  @media screen and (max-width: layouts.$xs-screen) {
    width: 80%;
  }
}

.account-name {
  display: inline;
  color: #797C8A;
}

.bank-branch {
  //color: colors.$medium-grey;
  font-style: italic;
  line-height: initial;
  font-size: 14px;
}

</style>