
import {defineComponent} from "vue";
import modal from "@/components/common/modal.vue";
import {useStore} from "vuex";

export default defineComponent({
name: "TAndC",
  components: {modal},
  props: {
    showButton: {
      type: Boolean,
      default: true,
    }
  },
  data() {
    return {
      showModal: false
    }
  },

  setup() {
    let $store = useStore()
    const countryCode = $store.state.countryCode

    return { countryCode }
  },

  methods: {
    toggleTC() {
      (this.$refs.tcModalRef as any).toggleShow();
    }
  }
})
