<template>
  <div class="home text-grey">
    <div class="step-progress-mobile mt-10" v-if="step !== 4">
      <step-progress-bar ref="StepProgressBarRef" :selected="step" @pick-step="pickStep"></step-progress-bar>
    </div>
    <section class="mobile-padding">
      <h2 class="text-grey ma-0 pa-heading">Pay Advance</h2>
      <h1 class="mt-4 mb-0 text-grey">Request</h1>
      <div class="mt-3">You can get an advance up to the amount you’ve earned in the current pay cycle.  </div>
      <div class="step-progress mt-10" v-if="step !== 4">
        <step-progress-bar ref="StepProgressBarRef" :selected="step" @pick-step="pickStep"></step-progress-bar>
      </div>
      <div class="aff-card enter-amount-card text-grey pa-9 mt-9" v-if="step === 1">
        <h3 class="my-0">Tell us the amount you need and we’ll check if it’s available.</h3>
        <div class="mt-3 admin-fee-info">
          <p class="ma-0 d-flex">
            <span class="icon-text copy2">The amount entered will be deducted from <b>your next pay, plus a $5 admin fee.</b></span>
          </p>
          <p class="mt-2 d-flex" v-if="isMinimumAmountError">
            <span class="iconify red " data-icon="ic:round-info" data-inline="false"></span>
            <span class="icon-text ml-2 copy2 red">This amount is not available. Please enter a different amount.</span>
          </p>

          <p class="mt-2 d-flex" v-if="requestFail">
            <span class="iconify red " data-icon="ic:round-info" data-inline="false"></span>
            <span class="icon-text ml-2 copy2 red">{{ errorMessage }}</span>
          </p>
        </div>
        <div class="mt-5">
          <div class="request-amount-wrapper">
            <div class="d-inline dollar">$</div>
            <input class="request-amount ml-3" type="number" :disabled="hasChecked" v-model.number="requestAmount">
          </div>

          <button class="btn-default checkAvailableButton ml-6" @click="checkAvailable" v-if="!hasChecked && requestAmount">
            <span class="mr-2" >
              <div class="inline-block" v-if="checkingRequest">
                <span class="iconify spin " data-inline="false" data-icon="ei:spinner" style="color: #0c96e4; font-size: 30px;"></span>
              </div>
            </span>
            <b>Check Availability</b>
          </button>
        </div>
        <div class="success-confirm mt-8 pa-4" v-bind:class="{'limited': limited}" v-if="hasChecked">
          <div class="precede2" v-if="!limited">Great! That amount is available.</div>
          <div class="precede2" v-else>Sorry, you only have up to ${{requestAmount}} available today. Do you still want to continue?</div>
          <div class="precede1 mt-4" v-if="hasUnapprovedTimesheets">You have unapproved timesheets. If your manager approves them, your available amount will increase.</div>
          <div class="precede1 mt-4 d-flex">
            <label class="cust-box-container" v-bind:class="[limited ? 'orange' : 'green']">
              <input type="checkbox" v-model="tcConfirmed">
              <span class="cust-box-checkmark"></span>
            </label>
            <span class="ml-6 d-inline">I agree to the <span class="tc-link" @click="openTC">terms and conditions</span> and understand that the amount requested will be deducted from my next pay plus a <b>$5 admin fee</b>.</span></div>
          <t-and-c ref="tcRef" :showButton="false"></t-and-c>
        </div>
      </div>
      <div v-if="step === 2">
        <select-bank></select-bank>
      </div>
      <div v-if="step === 3">
        <check-account @checkAccountComplete="checkAccountComplete"></check-account>
      </div>
      <div v-if="step === 4">
        <request-complete></request-complete>
      </div>
    </section>


<!--    step control button -->
    <div class="d-flex justify-space-between control-button">
      <button class="precede1 back-btn button-holder" v-if="step === 1"></button>

      <button class="precede1 back-btn" v-if="step !== 1 && step !== 4" @click="backStep">
        <span class="iconify" data-inline="false" data-icon="bi:arrow-left" style="font-size: 20px;"></span>
        <span class="ml-4 mr-6" v-if="step === 2">Back to Amount</span>
        <span class="ml-4 mr-6"  v-if="step === 3">Back to Account</span>
      </button>

      <button class="precede1 back-btn" v-if="step === 4" @click="gotoDashBoard">
        <span class="iconify" data-inline="false" data-icon="bi:arrow-left" style="font-size: 20px;"></span>
        <span class="ml-4">Back to Dashboard</span>
      </button>

      <button class="precede1 continue-btn" :disabled="availableAmount === 0 || tcConfirmed === false" v-if="step !== 4 && step !== 3" @click="nextStep">
        <span class="mr-4 ml-6">Continue</span>
        <span class="iconify" data-inline="false" data-icon="bi:arrow-right" style="font-size: 20px;"></span>
      </button>
    </div>
  </div>
</template>

<script lang="ts">
// import Intro from '@/components/Intro.vue'; // @ is an alias to /src
import {computed, defineComponent, ref} from 'vue';
import StepProgressBar from "@/components/StepProgressBar.vue";
import CheckAccount from "@/components/CheckAccount.vue";
import RequestComplete from "@/components/RequestComplete.vue";
import SelectBank from "@/components/SelectBank.vue";
import Remote from "@/remote";
import TAndC from "@/components/common/TAndC.vue";
import { useStore } from 'vuex';
import {GENERAL_ERROR, getErrorMessageFromCode, PRO_ERROR} from "@/components/constant";

export default defineComponent({
  name: 'Request',
  $refs: {
    StepProgressBarRef: StepProgressBar
  },
  components: {
    SelectBank,
    StepProgressBar,
    CheckAccount,
    RequestComplete,
    TAndC
    // Intro,
  },
  data () {
    return {
      checkingRequest: false,
      tcConfirmed: false,
      isMinimumAmountError: false,
      requestFail: false,
      errorMessage: '',
      hasUnapprovedTimesheets: false
    }
  },

  setup() {
    const limited = ref(false)
    const step = ref(1)
    const hasChecked = ref(false)
    const availableAmount = ref(0)
    const requestAmount = ref<number | null>()
    let $store = useStore()
    const minimumRequestAmount = computed(() => $store.state.minimumRequestAmount)
    const nextPaymentDate = computed(() => $store.state.nextPaymentDate)

    return { limited, step, hasChecked, availableAmount, requestAmount, minimumRequestAmount, nextPaymentDate }
  },

  mounted() {
    if (this.$store.state.featureChecked === false) {
      this.$router.push('/')
    }
  },

  methods: {
    pickStep(newStep: number) {
      this.step = newStep;
      console.log('picked in request', newStep)
    },

    checkAvailable() {
      //
      if (!this.requestAmount) {
        return;
      }

      this.isMinimumAmountError = false;
      this.requestFail = false;
      this.errorMessage = '';

      if (this.$store.state.minimumRequestAmount > this.requestAmount) {
        console.error('must bigger then min amount');
        this.isMinimumAmountError = true;
        return;
      }

      this.checkingRequest = true;
      Remote.RequestApproval(this.requestAmount!)
      .then((res) => {
        if (res.Response.IsOverLimit) {
          this.limited = true;
          this.requestAmount = res.Response.Limit;
          this.availableAmount = res.Response.Limit;
        } else {
          //
          this.availableAmount = res.Response.RequestAmount
        }

        if (res.Response.HasUnapprovedTimesheets) {
          this.hasUnapprovedTimesheets = res.Response.HasUnapprovedTimesheets
        }

        this.hasChecked = true;

        this.$store.state.requestAmount = this.requestAmount;

      }, err => {
        // console.error('err', err)
        this.requestFail = true;
        this.checkingRequest = false;
        if (err.response.data.Response?.ErrorCode) {
          if (err.response.data.Response?.ErrorCode === PRO_ERROR.AMOUNT_AVAILABLE_LESS_THAN_MIN_CONFIGURED) {
            this.errorMessage = `The current amount available $${err.response.data.Response.Limit} is less than the minimum request amount $${this.$store.state.minimumRequestAmount}. Please try again later.`
          } else {
            this.errorMessage = getErrorMessageFromCode(err.response.data.Response.ErrorCode, this.$store.state.nextPayCycleDate, this.$store.state.lastRequestDate)
          }
        } else {
          this.errorMessage = GENERAL_ERROR
        }

      })
      .finally(() => {
        this.checkingRequest = false;
      })

      console.log('checked available')
    },

    nextStep() {
      if (this.step < 4 ) {
        this.step++;
      } else {
        this.step = 1;
      }
      (this.$refs.StepProgressBarRef as typeof StepProgressBar).pick(this.step)
    },
    backStep() {
      this.step--;
      (this.$refs.StepProgressBarRef as typeof StepProgressBar).pick(this.step)

    },
    gotoDashBoard() {
      if (window.location.origin === 'https://payadvance.testaffinitylogon.com' ) {
        window.location.href = 'https://dashboard.testaffinitylogon.com/'
      } else {
        window.location.href = 'https://dashboard.affinitylogon.com/'
      }
    },

    checkAccountComplete() {
      // todo fire final request
      console.log('fire final request')
      this.step++
    },

    openTC() {
      console.log('clicked');
      (this.$refs.tcRef as any).toggleTC();
    }
  }
})
</script>

<style scoped lang="scss">
@use '../style/colors';
@use '../style/buttons';
@use '../style/layouts';

.enter-amount-card {
  width: 1040px;
  min-height: 342px;

  @media screen and (max-width: layouts.$md-screen) {
    width: 100%;
    padding: 18px;
    height: auto;
  }
}

.aff-card {

  box-sizing: border-box;
}

.info-dark-icon {
  display: inline-block;
  flex-shrink: 0;
  width: 15px;
  height: 16px;
  background: url("../style/icon/info-dark.svg");
  background-size: cover;
  vertical-align: middle;
}

.dollar {
  font-size: 56px;
  font-weight: bold;
  vertical-align: middle;
  color: colors.$dark-blue-app;

  @media screen and (max-width: layouts.$sm-screen) {
    font-size: 32px;
    width: 20px;
  }
}

.request-amount-wrapper {
  display: inline;
  @media screen and (max-width: layouts.$xs-screen) {
    display: block;
  }
}

.request-amount {
  width: 330px;
  height: 70px;
  font-size: 44px;
  font-weight: bold;
  color: colors.$dark-blue-app;
  border-radius: 4px;
  border: 1px solid #C8D1DB;
  vertical-align: middle;

  @media screen and (max-width: layouts.$sm-screen) {
    width: 150px;
    height: 44px;
    font-size: 32px;
  }
}

.step-progress {
  @media screen and (max-width: layouts.$xs-screen) {
    display: none;
  }
}

.step-progress-mobile {
  display: none;
  @media screen and (max-width: layouts.$xs-screen) {
    display: block;
    background: white;
  }
}

.admin-fee-info {
  @media screen and (max-width: layouts.$xs-screen) {
    margin-top: 25px;
  }
}

.checkAvailableButton {
  height: 45px;
  padding: 0 10px 0 0;
  b {
    vertical-align: middle;
  }

  @media screen and (max-width: layouts.$xs-screen) {
    margin-top: 10px;
    margin-left: 30px;
  }
}

.icon-text {
  display: inline-block;
  vertical-align: middle;
}

.success-confirm {
  background: #F6FFED;
  border: 1px solid colors.$light-green;
  color: colors.$green;
  border-radius: 4px;
}

.limited {
  color: colors.$yellow;
  background: #FFFBE6;
  border: 1px solid #FCE59A;
}

.control-button {
  //max-width: 940px;
  margin-top: 40px;
  button {
    vertical-align: middle;
  }

  span {
    vertical-align: middle;
  }

  @media screen and (max-width: layouts.$xs-screen) {
    margin: 20px;
  }
}

.back-arrow {
  display: inline-block;
  width: 31px;
  height: 31px;
  background: url('../style/icon/bi_arrow-left.svg');
  background-size: cover;
}

.right-arrow {
  display: inline-block;
  width: 23px;
  height: 15px;
  background: url('../style/icon/bi_arrow-right.svg');
  background-size: cover;
}

.back-btn {
  @extend .aff-btn;
  width: 230px;
  display: inline-block;
  font-size: 16px;
  border: 1px solid colors.$aff-dark-blue;
  border-radius: 40px;
  padding: 8px 20px;
  color: colors.$aff-dark-blue;
  background: transparent;

  &:hover {
    background: colors.$aff-dark-blue;
    color: white;
    border-color: transparent;
  }

  @media screen and (max-width: layouts.$xs-screen) {
    padding: 8px 10px;
    width: auto;
    font-size: 16px;

  }
}

.continue-btn {
  @extend .aff-btn;
  width: 230px;
  display: inline-block;
  font-size: 16px;
  border: 1px solid colors.$aff-dark-blue;
  border-radius: 40px;
  padding: 8px 20px;
  color: white;
  cursor: pointer;
  background: colors.$aff-dark-blue;

   &:hover {
     color: lighten(colors.$blue, 30%);
     background: colors.$aff-dark-blue;
     border: 1px solid colors.$aff-dark-blue;
   }

  &:disabled {
    background: #C9DCEF;
    color: white;
    border: none;
  }

  @media screen and (max-width: layouts.$xs-screen) {
    padding: 8px 10px;
    width: auto;
    font-size: 16px;

  }
}

.button-holder {
  opacity: 0;
}

.tc-link {
  text-decoration: underline;
  font-weight: bold;
  cursor: pointer;
}

.info-icon-size {
  font-size: 18px;
}

</style>